.container {
  max-width: 960px;
  margin: 20px auto;
  padding: 16px;
}

.uploader {
  border: 2px dashed #d1d5db;
  padding: 24px;
  border-radius: 12px;
  text-align: center;
}

.hint {
  color: #6b7280;
  margin-top: 8px;
}

.previews {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(160px, 1fr));
  gap: 12px;
  margin-top: 16px;
}

.thumb {
  position: relative;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  overflow: hidden;
}

.thumb img {
  width: 100%;
  height: 140px;
  object-fit: cover;
  display: block;
}

.thumb button {
  position: absolute;
  top: 6px;
  right: 6px;
}

button {
  margin-top: 16px;
  padding: 10px 16px;
  border-radius: 8px;
  border: 1px solid rgba(17, 24, 39, 0.1333333333);
  background: #111827;
  color: #fff;
}

button[disabled] {
  opacity: 0.6;
}

.error {
  color: #b91c1c;
  margin-top: 8px;
}

.result {
  margin-top: 24px;
  border: 1px solid #e5e7eb;
  border-radius: 12px;
  padding: 16px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.label {
  color: #6b7280;
  font-size: 12px;
}

.value {
  font-weight: 600;
  font-size: 18px;
}

.chip {
  background: #d1fae5;
  color: #065f46;
  padding: 6px 10px;
  border-radius: 999px;
  font-size: 12px;
}

.grid {
  margin-top: 12px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 16px;
}

.field {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.field-content {
  display: flex;
  align-items: center;
  gap: 8px;
}

.confidence-chip {
  font-size: 10px;
  padding: 2px 6px;
  border-radius: 4px;
  font-weight: 500;
}

.confidence-high {
  background: #d1fae5;
  color: #065f46;
}

.confidence-low {
  background: #fef3c7;
  color: #92400e;
}

.condition-badge {
  font-size: 10px;
  padding: 2px 6px;
  border-radius: 4px;
  font-weight: 500;
}

.condition-excellent {
  background: #d1fae5;
  color: #065f46;
}

.condition-good {
  background: #dbeafe;
  color: #1e40af;
}

.condition-fair {
  background: #fef3c7;
  color: #92400e;
}

.condition-poor {
  background: #fee2e2;
  color: #991b1b;
}

.low-confidence-warning {
  margin-top: 16px;
  padding: 12px;
  background: #fef3c7;
  border: 1px solid #f59e0b;
  border-radius: 8px;
  display: flex;
  align-items: center;
  gap: 8px;
}

.warning-icon {
  font-size: 16px;
}

.warning-text {
  color: #92400e;
  font-size: 14px;
}

.recs {
  margin-top: 12px;
}

.actions {
  display: flex;
  gap: 10px;
  margin-top: 12px;
}

.actions .primary {
  background: #2563eb;
}
